/* -----arrow------- */
.arrow{
  width: 80px;
  height: 80px;
  background-color: #fffffff2;
  place-items: center;
  display: flex;
  border-radius: 50%;
  filter:drop-shadow(2px 3px 6px rgba(0, 0, 0, 0.242));
  position: fixed;
  bottom: 30px;
  right: 20px;
  visibility: hidden;
}

.arrow_icon{
  margin: 0 auto; 
}

.js-active{
  visibility: visible;
}

/* ----burger------- */
.burger{
  position: fixed;
  top: 5px; left: 5px;
  z-index: 100;
  visibility: hidden;
}

.burger_box{
  /* padding: 2px; */
  height: 100%;
  width: 4rem;
  /* background-color: #eee; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 15px 0;
  cursor: pointer;
  z-index: 100;
  visibility: hidden;
}

.bar{
  background-color: #e41e53;
  width: 2.4rem;
  height: 4px;
  margin: 5px 2px;
  border-radius: 0.5rem;
  z-index: 100;
  visibility: inherit;
}

.menu{
  width: 25%;
  height: 100%;
  min-width: 400px;
  position: fixed;
  top: 0;
  left: 0;

  background: linear-gradient(180deg,rgb(146, 207, 240),rgba(203, 36, 36, 0.3));
  z-index: 10;
  visibility: inherit;
}

.hidden{
  display: none;
}

.visible{
  display: inherit;
}

.navbar_menu_list{
  width: 100%;
  /* background-color: #b15a5a; */
  margin: 100px 0;
}

.row{
  font-family: "Jersey 10", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 38px;

  padding: 10px 30px;
  cursor: pointer;
}

.row:hover{
  background-color: #d8e4a9c9;
  
}


/* --------------animation------------------ */
/* clicked */
.bar.clicked:nth-child(1){
  transform:  rotate(45deg) translate(1.0em, 0.9em);
  transition: ease-out 0.5s;
}
.bar.clicked:nth-child(2){
  /* transform:  translate(0.0em, 0.55em) scale(0.1) ; */
  transform:  translate(0.0em, 0.55em);
  opacity: 0;
  transition: ease-out 0.5s;
}
.bar.clicked:nth-child(3){
  transform:  rotate(-45deg) translate(0.3em, -0.2em);
  transition: ease-out 0.5s;
}

/* unclikced */
.bar.unclicked{
  transform: rotate(0) translate(0);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.2575) 0.5s;
}

.menu.visible{
  animation: menuAnime 0.6s;
}

@keyframes menuAnime{
  from{
      transform: translateX(-100%);
      
      transition: cubic-bezier(.44,.47,.84,1.66) 2.5s;
   }

   0%{
      border-radius: 0% 70% 70% 0%;
   }
   70%{
      border-radius: 0% 50% 50% 0%;
   }
   80%{
      border-radius: 0% 40% 40% 0%;
   }
   99%{
      border-radius: 0% 10% 10% 0%;
   }
  }




/* ----Hero------ */
.hero{
  height: 750px;
  display: flex;
  place-items: center;
  --gra_degree:10deg;
  background: linear-gradient(var(--gra_degree), rgb(249, 60, 133), rgb(232, 116, 164), rgb(255, 240, 197),rgb(243, 188, 106));
  background-size: 250% 250%;
  animation: backgroundAnimation 8s ease-in infinite; 
  padding: 5%;
  position: relative;
}
@keyframes backgroundAnimation{
  0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 60%;
    }
    100% {
      background-position: 0% 50%;
  
    }
}



.hero_container{
  width: 100%;
  height: 100%;
  position: relative;
  /* background-color: #599ede8e; */
}

.hero_back{
  position: absolute;
  z-index: 2;
}

.backTitle{
  font-family: "Silkscreen", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 110px;
  color: #fcfcfc8e;
  margin: 0;
  padding-left: 50px;
}

.hero_front{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  z-index: 3;
}
.hero_front_content{
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero_pixImg{
  width: 50%;
  margin-top: 25px;
}
