/* ~~~~~~~~~~~~~header-wave~~~~~~~~~~~~~~` */
.custom-shape-divider-bottom-1714892597 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.custom-shape-divider-bottom-1714892597 svg {
  position: relative;
  display: block;
  width: calc(144% + 1.3px);
  height: 219px;
}

.custom-shape-divider-bottom-1714892597 .shape-fill {
  fill: #FFFFFF;
}

/** For mobile devices **/
@media (max-width: 767px) {
  .custom-shape-divider-bottom-1714892597 svg {
      width: calc(100% + 1.3px);
      height: 139px;
  }
}
