.kind_container{
  width: min(100% - 2rem, 500px);
  margin-inline: auto;
  color: #eee;
}
.t_color{
  color: #eee;
}
.kind_content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}


.kind_content > h2{
  text-align: center;
}

.kind_table{
  border-collapse: collapse;
  font-size: 20px;
}
.kind_table > tr > th{
  white-space: nowrap;
  width: 160px;
  text-align: right;
}
.kind_table > tr > td{
  width: calc(464px - 160px);
  padding: 0 4%;
  white-space: nowrap;
  letter-spacing: -0.4rem;
}

@media screen and (max-width: 360px) {
  .kind_table > tr{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-bottom: 10px;
  }
  .kind_table > tr >th,.kind_table > tr >td{
    text-align: left;
    padding: 0;
    width: 80%;
}

.kind_layout{
  grid-template-columns: 
  repeat(auto-fit, minmax(90%, 1fr));
}

